<template>
  <div>
    <el-row>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>
          <a href="/">合同列表</a>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="mt15" :gutter="5">
      <el-col :xl="1" :lg="1" :md="3" :sm="4" :xs="4">
        <el-button size="mini" type="success" @click="reload">刷新</el-button>
      </el-col>
      <el-col :xl="1" :lg="1" :md="3" :sm="4" :xs="4">
        <el-button size="mini" type="warning" @click="$router.push('/admin/task/add_contract')">新增</el-button> 
      </el-col>
    </el-row>
    <el-row class="mt15" :gutter="10">
      <el-col :span="4" :sm="6" :xs="8">
        <el-date-picker
          v-model="date_time"
          type="daterange"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          format="yyyy-MM-dd HH:mm:ss"
          value-format="yyyy-MM-dd HH:mm:ss"
        ></el-date-picker>
      </el-col>

      <el-col :span="2">
        <el-button icon="el-icon-search" @click="searchBtn">查询</el-button>
      </el-col>
    </el-row>
    <el-table class="mt15" :data="contract_list" border style v-loading="loading">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="index" label="编号" fixed width="60" align="center" header-align="center"></el-table-column>
      <el-table-column label="id" prop="id" fixed align="center" header-align="center"></el-table-column>
      <el-table-column fixed prop="title" label="标题" align="center" header-align="center"></el-table-column>
       <el-table-column prop="created_at" label="创建日期" align="center" header-align="center">
      </el-table-column>
      <el-table-column prop="status" label="状态" width="120" align="center" header-align="center">
        <template slot-scope="scope">
          <el-button type="success" v-if="scope.row.status==1" size="mini">上架中</el-button>
          <el-button type="error" v-if="scope.row.status==2" size="mini">已下架</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="city" label="审核状态" width="120" align="center" header-align="center">
        <template slot-scope="scope">
          <el-button type="success" v-if="scope.row.audit_ret==1" size="mini">审核成功</el-button>
          <el-button type="warning" v-if="scope.row.audit_ret==3" size="mini">审核中</el-button>
          <el-button type="error" v-if="scope.row.audit_ret==2" size="mini">审核失败</el-button>
        </template>
      </el-table-column> 
      <el-table-column fixed="right" label="操作" width="200" height="200">
        <template slot-scope="scope">
          <el-button type="text" size="mini" @click="$router.push({ path : '/admin/task/add_contract', query: { id : scope.row.id}})">编辑</el-button>
          <el-button type="text" size="mini" @click="del(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="mt15">
      <el-pagination layout="prev, pager, next" :total="total" :page-size="limit" @current-change="pageChange" :background="true"></el-pagination>
    </div>
    <el-dialog
        title="报名人员"
        :visible.sync="signVisible"
        width="700px"
        :center="true"
        :close-on-click-modal="false"
      >
        
      </el-dialog>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      signVisible : false,
      signData:[],
      search : {
      },
      date_time: "",
      value: 0,
      options:[1],
      total: 1000,
      limit: 10,
      page:1,
      loading:true,
      contract_list:[],
    };
  },
  created(){
     this.loadData({});
  },
  mounted() {
    this.loading = false
  },
  methods: {
    loadData (search) {
      console.log(search)
      let sea = search ? search : {};
      sea.page=this.page;
      sea.limit=this.limit
      this.$api.get('enterprise/get_contract_list',sea, res=>{
        this.contract_list=res.data.data
        this.total = res.data.data.total
      });
    },
    dateRes (date) {
        var d = new Date(date);  
        var  resDate = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds(); 
        return resDate;
    },
    searchBtn(){
      var data = {
        begin_time:this.date_time[0],
        end_time:this.date_time[1],
      };
      this.search=data;
      this.loadData(data)
    },
    handleClick(row) {
      console.log(row);
    },
    del(id){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$api.post('/enterprise/del_contract', {id : id}, res=> {
            if(res.data.bol)
            {
              this.$message.success(res.data.msg);
              this.loadData({});
            }else{
              this.$message.error(res.data.msg);
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    pageChange(e)
    {
      this.page=e;
      this.loadData({});
    },
    reload()
    {
      window.localtion.reload();
    }
  }
};
</script>

<style>
  
</style>



